













import { Vue } from 'vue-property-decorator';
import axios from 'axios';
import { Humidity } from '../../types';

export default Vue.extend({
    data() {
        return {
            loading: false,
            humidityData: [],
            timestamps: [],
            series: [
                {
                    data: [],
                },
            ],
            chartOptions: {
                chart: {
                    type: 'area',
                    stacked: false,
                    height: 350,
                    zoom: {
                        type: 'x',
                        enabled: true,
                        autoScaleYaxis: true,
                    },
                },
                dataLabels: {
                    enabled: false,
                },
                markers: {
                    size: 0,
                },
                fill: {
                    type: 'gradient',
                    gradient: {
                        shadeIntensity: 1,
                        inverseColors: false,
                        opacityFrom: 0.5,
                        opacityTo: 0,
                        stops: [0, 90, 100],
                    },
                },
                yaxis: {
                    min: 0,
                    max: 100,
                    labels: {
                        formatter: (val: number) => val.toFixed(0),
                    },
                    title: {
                        text: 'Percentage',
                    },
                },
                xaxis: {
                    title: {
                        text: 'Time',
                    },
                },
                tooltip: {
                    shared: false,
                    y: {
                        formatter: (val: number) => val.toFixed(0),
                    },
                },
            },
        };
    },

    created() {
        // this.loading = true;
        // axios
        //     .get('http://localhost:3000/api/humidity/10')
        //     .then((response: any) => {
        //         this.humidityData = response.data.map((el: any) => el.value.toFixed(2));
        //         this.timestamps = response.data.map((el: any) => {
        //             const date = new Date(el.timestamp);
        //             /* eslint-disable */
        //             const dateStr =
        //                 ('00' + date.getHours()).slice(-2) +
        //                 ':' +
        //                 ('00' + date.getMinutes()).slice(-2) +
        //                 ':' +
        //                 ('00' + date.getSeconds()).slice(-2);
        //             return dateStr;
        //         });
        //         this.updateChart();
        //         this.loading = false;
        //     })
        //     .catch((error: any) => {
        //         console.log(error);
        //     });
    },

    methods: {
        updateChart() {
            this.$refs.humidityChart.updateSeries([
                {
                    data: this.$data.humidityData,
                },
            ]);

            this.$refs.humidityChart.updateOptions({
                xaxis: {
                    categories: this.$data.timestamps,
                },
            });
        },
    },
    watch: {
        /* eslint-disable */
        humidityData: function() {
            this.updateChart();
        },
    },

    sockets: {
        humidity(data: string) {
            const message: Humidity = JSON.parse(data);
            const value = message.value.toFixed(2);
            const [timestamp, _] = new Date(message.at).toTimeString().split(' ');

            if (this.$data.humidityData.length > 9) {
                this.$data.humidityData.shift();
                this.$data.timestamps.shift();
            }

            this.$data.humidityData.push(value);
            this.$data.timestamps.push(timestamp);
        },
    },
});
